:root {
  --black: #131519;
  --black-border: #111317;
  --blue: #0045f2;
  --lightning-blue: #2a49fb;
  --gray: #1f2228;
  --dark-gray: #1a1c21;
  --blue-text: #6d69ff;
  --gray-text: #838485;
  --white-text: #fdfdfd; }

html {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Helvetica Neue", Arial, sans-serif;
  font-size: 62.5%;
  /* =10px */ }

body {
  background-color: #000;
  color: #fdfdfd;
  font-size: 1.2rem;
  /* =12px */
  height: 100%;
  line-height: 1.5rem;
  margin: 0;
  overflow: hidden;
  width: 100%; }

*,
::after,
::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

[hidden],
.hidden {
  display: none !important; }

.noselect {
  user-select: none !important; }

button {
  background-color: #131519;
  border: none;
  cursor: pointer;
  color: #fdfdfd;
  font-size: 1rem;
  outline: none;
  padding: 0.4rem 0.8rem; }
  button:hover, button:active {
    background-color: #1f2228; }
  button:disabled {
    cursor: default;
    opacity: 0.5; }

select {
  appearance: none !important;
  -ms-appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  background-color: #131519;
  background-image: url("/images/chevron-down-solid.svg");
  background-position: right center;
  background-repeat: no-repeat;
  background-size: 1.25em 10px;
  border-color: #131519;
  border-radius: 0.3rem;
  color: #f4f4f4;
  line-height: 1em;
  min-height: 20px;
  outline: none;
  padding: 0 1.5em 0 0.3em;
  text-indent: 0.01px;
  text-overflow: ""; }
  select::-ms-expand {
    display: none; }
  select > option {
    background-color: #131519; }

a {
  color: #fdfdfd;
  text-decoration: none; }
  a:hover, a:visited {
    color: #fdfdfd; }

.app-layout {
  color: #fdfdfd;
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100%; }
  .app-layout__header {
    background: transparent;
    position: fixed;
    top: 8px;
    left: 8px;
    z-index: 99999; }
  .app-layout__main {
    align-items: stretch;
    display: flex;
    justify-content: space-between;
    flex: 1 0 auto;
    position: relative;
    width: 100%; }
  .app-layout__footer {
    background-color: #1f2228;
    flex: 0 0 auto;
    min-height: 3rem; }
